<template>
    <div>
        <p class="vui-page-title">check</p>
        <v-switch>checkedfalse</v-switch>
        <v-switch checked="true">checkedtrue</v-switch>

        <p class="vui-page-title">namevalue</p>
        <v-switch name="test">nametest</v-switch>
        <v-switch value="2">name2</v-switch>

        <p class="vui-page-title">changedisabledfalseundefined0falsechange</p>
        <v-switch @change="onChange">disabled</v-switch>
        <br>
        <v-switch disabled="false" @change="onChange">disabledfalse</v-switch>
        <br>
        <v-switch :disabled="false" @change="onChange">disabledfalse</v-switch>
        <br>
        <v-switch :disabled="0" @change="onChange">disabled0</v-switch>
        <br>
        <v-switch disabled @change="onChange">disabled</v-switch>
        <br>
        <v-switch disabled="true" @change="onChange">disabledtrue</v-switch>
        <br>
        <v-switch :disabled="true" @change="onChange">disabledtrue</v-switch>
        <br>
        <v-switch disabled="disbaled" @change="onChange">disabled</v-switch>
    </div>
</template>

<script>
    export default {
        name: 'switch',
        data() {
            return {
                checked: false
            }
        },
        methods: {
            onChange: function (event, checked) {
                console.log('checked==>', checked)
            }
        }
    }

</script>

